<template>
	<v-card class="ma-md-6 ma-sm-3">
		<v-card-title class="py-2">
			<v-icon class="mr-3"></v-icon>
			Daftar Pelajaran
		</v-card-title>
		<v-divider></v-divider>
		<v-card-text>
			<TambahPelajaran/>

			<v-form ref="form" v-model="valid" class="mt-6">
				<v-row dense>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Mata Pelajaran"
							outlined
							dense
							v-model="dataFilter.mapel"
							:items="data"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Kelas"
							outlined
							dense
							v-model="dataFilter.kelas"
							:items="data"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-autocomplete
							label="Kurikulum"
							outlined
							dense
							v-model="dataFilter.kurikulum"
							:items="data"
							required
							:rules="requiredRule"
						></v-autocomplete>
					</v-col>
					<v-col cols="12" md="3" sm="6" class="py-0">
						<v-btn
							type="submit"
							:disabled="!valid"
							@click.prevent="validate"
							color="primary"
							depressed>
							filter
						</v-btn>
					</v-col>
				</v-row>
			</v-form>

			<DataPelajaran/>
		</v-card-text>
	</v-card>
</template>

<script>
	import { mapState } from 'vuex'
	import DataPelajaran from '@/components/Pelajaran/DataPelajaran'
	import TambahPelajaran from '@/components/Pelajaran/TambahPelajaran'

	export default{
		data: () => ({
			valid: true,
			requiredRule: [v => !!v || 'Data harus diisi'],
			data: ['foo'],
			dataFilter: {
				mapel: '',
				kelas: '',
				kurikulum: ''
			}
		}),

		components: {
			TambahPelajaran,
			DataPelajaran
		},

		created() {
			this.$store.dispatch('dataKurikulum/getDataKurikulum')
			this.$store.dispatch('dataKelas/getDataKelas')
		},

		computed: {
			...mapState('dataKurikulum', ['dataKurikulum']),
			...mapState('dataKelas', ['dataKelas'])
		},

		methods: {
			validate () {
				if (this.$refs.form.validate()) {
					console.log(this.dataFilter)
				}
			}
		}
	}
</script>